*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'montseratt', sans-serif;
}

.app {
  
  background-image: url(./asset/piccold.jpg);
  background-size: cover;
  background-position: bottom;
  transition: 0.4s ease;

}

.app.warm {
  background-image: url(./asset/picwarm.jpg);
}


main {
  min-height: 100vh;
  background-image: linear-gradient( to bottom, rgba(0,0,0,0.2),rgba(0,0,0,0.75));
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: aliceblue;
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0,0,0,0.2) ;

  color: black;
  font-size: 20px;

  transition: 0.4s;
}

.search-box .search-bar:focus{
  background-color: rgba(225, 225, 225, 0.75);


}


.location-box .location {
  color:azure;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, .5);

}

.location-box .date {
  color:azure;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, .5);

}

.weather-box {
  text-align: center;

}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(225,225, 225, .2);
  border-radius: 16px;

  padding: 15px 25px;


  color:azure;
  font-size: 102px;
  font-weight: 900;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, .5);
  box-shadow: rgba(0 0 0 rgba(0,0,0,0.2));

}

.weather-box .weather {
  color:azure;
  font-size: 40px;
  font-weight: 700;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, .5);

}
